/**
 * Process the "Title" expression provided in yaml.
 * Example: 'Status in employment {$TIME_PERIOD}, [DIN, 14, Bold, Italics, LEFT]',
 *
 * @todo Review style parsing method as it is not limited to 5 params only:
 *   Font type, size, emphasis (bold, italics, etc.) and location (LEFT, CENTER, RIGHT)
 *   can be specified in square-brackets ([]) following any text, title, note or label specification.
 * @todo What is DIN param ? (can not find info in requirements, just ignore for now)
 * @todo fix variable params e.g. {$TIME_PERIOD}
 *
 * @param {String} titleExpr
 * @param {Object} dimensions
 * @returns {Object}
 */
export var parseTextExpr = function (textExpr, dimensions) {
    // clean up title
    var text = textExpr.replace(/,[\s]?\[(.*?)\]$/, '').trim();
    //const textWithValues = text.replace(/\{\$(.*?)\}/g, (match, p1) => {
    var textWithValues = text.replace(/\{(.*?)\}/g, function (match, p1) {
        var valueAttribute = 'name';
        // replace {$VARIABLE} with actual value
        if (p1.startsWith('$')) {
            p1 = p1.substring(1);
        }
        else {
            // replace {VARIABLE} with id
            valueAttribute = 'id';
        }
        var dimension = dimensions.find(function (item) { return item.id === p1; });
        if (!dimension) {
            console.warn("Dimension with id ".concat(p1, " not found"));
            return match;
        }
        else {
            if (dimension.values.length > 1) {
                if (dimension.id === 'TIME_PERIOD') {
                    // if concept is TIME_PERIOD, we use the first and last value
                    var date1Str = dimension.values[0][valueAttribute];
                    var date2Str = dimension.values[dimension.values.length - 1][valueAttribute];
                    var date1 = new Date(date1Str);
                    var date2 = new Date(date2Str);
                    var date1Text = "".concat(date1.getFullYear());
                    var date2Text = "".concat(date2.getFullYear());
                    if (date1Str.split('-').length >= 2) {
                        // if year and month is provided
                        date1Text = "".concat(date1.toLocaleString('default', { month: 'short' }), " ").concat(date1Text);
                        date2Text = "".concat(date2.toLocaleString('default', { month: 'short' }), " ").concat(date2Text);
                    }
                    if (date1Str.split('-').length >= 3) {
                        // if year, month and day is provided
                        date1Text = "".concat(date1.getDay(), " ").concat(date1Text);
                        date2Text = "".concat(date2.getDay(), " ").concat(date2Text);
                    }
                    if (date1 < date2) {
                        // if date1 is before date2
                        return "".concat(date1Text, " - ").concat(date2Text);
                    }
                    else {
                        // if date2 is before date1
                        return "".concat(date2Text, " - ").concat(date1Text);
                    }
                }
                else {
                    // we return a comma separated list of values
                    return dimension.values.map(function (item) { return item[valueAttribute]; }).join(', ');
                }
            }
            else {
                // we return the value
                return dimension.values[0][valueAttribute];
            }
        }
    });
    return textWithValues;
};
export var parseOperandTextExpr = function (operandExpr, data, attributes) {
    var textWithValues = String(operandExpr).replace(/\{(.*?)\}/g, function (match, p1) {
        var valueAttribute = 'name';
        // replace {$VARIABLE} with actual value
        if (p1.startsWith('$')) {
            p1 = p1.substring(1);
        }
        else {
            // replace {VARIABLE} with id
            valueAttribute = 'id';
        }
        var attribute = attributes.find(function (item) { return item.id === p1; });
        if (!attribute) {
            throw new Error("Attribute with id ".concat(p1, " not found"));
        }
        else {
            var value = attribute.values.find(function (item) { return item.name === data[attribute.id]; });
            if (value) {
                if (attribute.id === 'UNIT_MULT') {
                    return Math.pow(10, parseInt(value[valueAttribute]));
                }
                return value[valueAttribute];
            }
            else {
                return '';
            }
        }
    });
    return textWithValues;
};
